.program-list {
  width: 320px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.program-list__option {
  position: relative;
  padding-right: 10px;
  padding-left: 45px;
  &:last-child {
    margin-bottom: 0;
  }
}

.program-list__option label {
  cursor: pointer;
}

.program-list__option--radio {
  margin-bottom: 5px;
  .set-vertical-padding(10px);
}

.program-list__option--radio label {
  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: -1px;
    width: 30px;
    height: 30px;
    border: 1px solid @border-very-lighter-grey;
    border-radius: 50%;
  }

  &:hover::before,
  &:focus::before {
    border-color: #bcbcbc;
  }

  &:active {
    opacity: 0.3;
  }
}

.program-list__option--checkbox {
  margin-bottom: 8px;
  .set-vertical-padding(8px);
}

.program-list__option--checkbox label {
  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: -1px;
    width: 30px;
    height: 30px;
    border: 1px solid @border-very-lighter-grey;
  }

  &::after {
    // content: "";
    // position: absolute;
    // top: -11px;
    // left: -2px;
    // width: 30px;
    // height: 30px;
    // border: 2px solid @green;
    // transform: rotate(42deg);
  }

  &:hover::before,
  &:focus::before {
    border-color: #bcbcbc;
  }

  &:active {
    opacity: 0.3;
  }
}

.program-list__option input:checked+label::after {
  content: "";
  position: absolute;
  top: 13px;
  left: 8px;
  width: 12px;
  height: 12px;
  background-color: @green;
  border-radius: 50%;
}

.program-list__input {
  display: none;
}

@media (min-width: @tablet-width) {
  .program-list {
    width: 631px;
    margin: 0 40px 65px;
  }

  .program-list__option--radio {
    margin-bottom: 13px;
    padding-left: 68px;
  }

  .program-list__option--radio label {
    &::before {
      top: 1px;
      left: 8px;
      width: 40px;
      height: 40px;
    }
  }

  .program-list__option--radio input:checked + label::after {
    top: 12px;
    left: 19px;
    width: 18px;
    height: 18px;
  }

  .program-list__option--checkbox {
    margin-bottom: 13px;
    padding-left: 59px;
    .set-vertical-padding(14px);
  }

  .program-list__option--checkbox label {
    &::before {
      top: 5px;
      left: 0px;
      width: 40px;
      height: 40px;
    }
  }
}

@media (min-width: @desktop-width) {
  .program-list {
    width: 570px;
    margin: 0;
  }
}
